import React from 'react'

const Home = props => {
  const stylings = {
    width: '100%'
  }
  return (
    <img src='https://cdn3.wpbeginner.com/wp-content/uploads/2017/08/socialnetworkbuddypress.png' style={stylings}></img>
  )
}

export default Home



